<template>
  <div class="edm-view">
    <div class="left">
      <h3></h3>
      <ul class="lists">
        <li @click="typeIndex = 0" :class="typeIndex == 0 ? 'selected' : ''">
          <img src="//fes.qyerstatic.com/FgyppAyh7U0oimVZxz_jybfeJG7R?imageslim" alt />
          <p></p>
        </li>
        <li @click="typeIndex = 1" :class="typeIndex == 1 ? 'selected' : ''">
          <img src="//fes.qyerstatic.com/FmdOgtMt1KRUZ0gkBmy3vRWrjhPh?imageslim" alt />
          <p></p>
        </li>
      </ul>
    </div>
    <div class="right">
      <div class="content" id="content" v-html="list[typeIndex]"></div>
      <!--  -->
      <button @click="downFile">edm</button>
      <button @click="sendEmail" class="email"></button>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form>
        <el-form-item label="">
          <el-input v-model="titlename" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="link" placeholder="https://www.qyer.com" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="color" placeholder="#f60" autocomplete="off"></el-input>
          <div id="color-picker-view">
            <span>{{color}}</span>
            <em id="color-picker" :style="'background:'+color"></em>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="getLinkInfo"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { dataObj } from "./edm.js";

//
let pageData = dataObj();
export default {
  data() {
    return {
      list: pageData.list,
      typeIndex: 0,
      titlename: "",
      link: "",
      color: "#ff5621",
      dialogFormVisible: false,
      html: ""
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.init();
    });
  },
  watch: {
    typeIndex() {
      setTimeout(() => {
        this.init();
      }, 1000);
    }
  },
  methods: {
    init() {
      console.log("123142");

      let links = document.querySelectorAll("#content a");

      links.forEach(item => {
        item.addEventListener("click", e => {
          e.preventDefault();
          this.openLink();
        });
      });
    },
    // test
    sendEmail() {
      let html = document.querySelector("#content_html").innerHTML;
      // console.log(html);
      let _h = `<!DOCTYPE html><html><head><meta charset="utf-8" /><meta name="viewport" content="width=device-width, initial-scale=1" /></head><body>${html}</body></html>`;

      window.open(
        "mailto:sample@fly63.com?subject=test&cc=sample@hotmail.com&subject=&body=" +
          _h
      );
    },
    // 
    getLinkInfo() {
      if (this.link == "") {
        this.$message.error("");
        return;
      }
      this.dialogFormVisible = false;
      let link = document.querySelector(".table_btn_hover");
      let linkDiv = link.querySelector("div");
      linkDiv.innerHTML = this.titlename;
      link.setAttribute("href", this.link);
      linkDiv.style.cssText += "background:" + this.color;
    },
    // 
    openLink() {
      this.dialogFormVisible = true;
      if (document.querySelector("#colorpicker_pen")) return;
      setTimeout(() => {
        new Colorpicker({
          el: "color-picker",
          color: "#ff5621",
          change: (elem, hex) => {
            this.color = hex;
          }
        });
      }, 1000);
    },
    downFile() {
      let html = document.querySelector("#content_html").innerHTML;
      // console.log(html);
      let _h = `<!DOCTYPE html><html><head><meta charset="utf-8" /><meta name="viewport" content="width=device-width, initial-scale=1" /></head><body>${html}</body></html>`;
      this.download("edm.html", _h);
      this.html = _h;
    },
    download(filename, text) {
      var element = document.createElement("a");
      element.setAttribute(
        "href",
        "data:text/plain;charset=utf-8," + encodeURIComponent(text)
      );
      element.setAttribute("download", filename);

      element.style.display = "none";
      document.body.appendChild(element);

      element.click();

      document.body.removeChild(element);
    }
  }
};
</script>
<style lang="less" scoped>
.edm-view {
  display: flex;
  flex-direction: row;
  .left {
    width: 204px;
    background: #000;
    padding: 0 2px;
    height: calc(~"100vh - 60px");
    h3 {
      height: 40px;
      font-size: 20px;
      line-height: 40px;
      text-align: center;
      background: #409eff;
      color: #fff;
    }
    .lists {
      li {
        position: relative;
        margin-bottom: 20px;
        cursor: pointer;
        &.selected {
          &:after {
            content: "";
            position: absolute;
            right: -40px;
            top: 50%;
            margin-top: -20px;
            width: 0;
            height: 0;
            border: 20px solid transparent;
            border-left: 20px solid #000;
          }
        }
        p {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          line-height: 30px;
          line-height: 30px;
          text-align: center;
          background: rgba(64, 158, 255, 0.8);
          color: #fff;
          font-weight: bold;
        }
        img {
          vertical-align: top;
        }
      }
    }
  }
  .right {
    margin-left: 30px;
    flex: 1;
    height: calc(~"100vh - 60px");
    overflow: auto;
    background: #08f5e470;
    position: relative;
    button {
      position: absolute;
      right: 50%;
      top: 20px;
      width: 100px;
      height: 30px;
      background: #409eff;
      color: #fff;
      margin-right: -440px;
    }
    .email {
      top: 120px;
    }
    .content {
      width: 640px;
      height: calc(~"100vh - 60px");
      margin: 0 auto;
      overflow: auto;
    }
  }
}
.el-form-item__label {
  display: block;
  float: inherit;
}
.el-form-item__content {
  display: flex;
  flex-direction: row;
}
#color-picker-view {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  span {
    line-height: 40px;
    margin-right: 10px;
  }
}
#color-picker {
  width: 40px;
  height: 40px;
  background: #ff5621;
  margin-top: 10px;
}
</style>


